import { Navigate } from "react-router-dom";
import React from "react";
import { useSelector } from "react-redux";
import { getQuestion } from "../Actions/Quiz/QuizAction";

const PrivateRoute = ({ component: RouteComponent }) => {
    
    const isLoggedIn = useSelector((state) => state.loggedIn.isLoggedIn);
    const connErr = useSelector((state) => state.qStore.connctionError);

    // getQuestion({updated:true});

    if(isLoggedIn) {
        // if(connErr) return <p>connection err</p>
        if(connErr) return <p>heroku connection err</p>
        return <RouteComponent />
    }
    return <Navigate to="/login"/>
}

export default PrivateRoute;