import axios from "axios";
import store from "../../store";
import { ADD_URL, CONFIG, DELETE_URL, GET_Q_SPECIFIC, GET_Q_URL, SUCCESS, UPDATE_URL } from "../../Utils/Constants";
import { connected, connectionError, loadQuestions } from "./QuizSlice";


// --------------- insert question ---------------

export const addQuestion = async (body) => {
    await axios.post(ADD_URL, body, CONFIG)
        .then((res) => {
            if (res.data.status === SUCCESS) {
                getQuestion({ updated: true });
                alert('Question added successfully.')
            }
        })
}

// --------------- get questions ---------------

export const getQuestion = async (updated = false) => {

    //console.log(sessionStorage.getItem('questions'));

    if (sessionStorage.getItem('questions') == undefined || updated) {
        //console.log('getQuestions');
        try {
            await axios.get(GET_Q_URL, CONFIG)
                .then((res) => {
                    sessionStorage.setItem('questions', JSON.stringify(res.data.questions));
                })
                .then(() => {
                    store.dispatch(loadQuestions());
                })

            store.dispatch(connected());
        }
        catch (err) {
            console.debug(err);
            store.dispatch(connectionError());
            setTimeout(() => {
                getQuestion({});
                console.debug("hello");
            }, 6000);
        }
    } else {
        store.dispatch(loadQuestions());
    }

}

// --------------- get specific  ---------------

export const getSpecificQuestion = (obj) => {
    const question = axios.post(GET_Q_SPECIFIC, obj, CONFIG)
        .then((res) => {
            return res.data.question;
        })

    return question;
}

// --------------- delete question ---------------

export const deleteQuestion = async (obj) => {
    //console.log(obj);

    await axios.delete(DELETE_URL, { data: obj }, CONFIG)
        .then((res) => {

            if (res.data.status === SUCCESS) {
                //console.log(res.data.status);
                getQuestion({ updated: true });
            }
        })
}

// --------------- update question ---------------

export const updateQuestion = async (obj) => {
    //console.log(obj);
    await axios.put(UPDATE_URL, obj, CONFIG)
        .then((res) => {
            if (res.data.status === SUCCESS) {
                //console.log(res.data.status);
                getQuestion({ updated: true });
                alert('updated successfully. ')
            }
        })
}
