import { createSlice } from "@reduxjs/toolkit";

export const QuizSlice = createSlice({
    name : 'questions',
    initialState : {
        questions : [],
        connectionError: false
    },
    reducers : {
        loadQuestions: (state) => {
            state.questions = JSON.parse(sessionStorage.getItem('questions'));
        },
        connectionError : (state) => {
            state.connectionError = true;
        },

        connected : (state) => {
            state.connectionError = false;
        },
    }
});

export const { loadQuestions, connectionError, connected} = QuizSlice.actions;
export default QuizSlice.reducer;