import './App.css';
import AddQuestions from './Layouts/AddQuestions/AddQuestions';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Route, Routes, BrowserRouter } from 'react-router-dom';
import Questionboard from './Layouts/Questionboard/Questionboard';
import { Provider } from 'react-redux';
import store from './store';
import 'bootstrap/dist/css/bootstrap.min.css';
import AboutUs from './Layouts/AboutUs/AboutUs';
import News from './Layouts/News/News';
import Admin from './Layouts/Admin/Admin';
import AddQForm from './Components/Form/AddQForm';
import PrivateRoute from './routes/PrivateRoute';
import PublicRoute from './routes/PublicRoute';
import Register from './Layouts/authentication/Register';
import Login from './Layouts/authentication/Login';
import { getQuestion } from './Actions/Quiz/QuizAction';

function App() {
  getQuestion({});
  return (
    <>
    <Provider store={store}>
      <BrowserRouter>
        <Routes>
        <Route path='/' element={<Questionboard />}></Route>

          <Route path='/login' element={<PublicRoute component={Login}/>}></Route>
          <Route path='/register' element={<Register/>}></Route>

          <Route path='/quiz' element={<Questionboard />}></Route>
          <Route path='/addq' element={<AddQuestions />}></Route>
          <Route path='/admin/edit/:id' element={<AddQForm />}></Route>
          <Route path='/aboutus' element={<AboutUs  />}></Route>
          <Route path='/news' element={<News />}></Route>

          <Route path='/admin' element={< PrivateRoute component={Admin} />}></Route>
        </Routes>
      </BrowserRouter>
      </Provider>
    </>
  );
}

export default App;
