import React, { useEffect, useState } from "react";
import { Button, Col, Container, Figure, Form, FormControl, InputGroup, Row } from "react-bootstrap";
import { useParams } from "react-router-dom";
import { addQuestion, getSpecificQuestion, updateQuestion } from "../../Actions/Quiz/QuizAction";
import QuizNavBar from "../../Components/NavBar/QuizNavBar";

const AddQForm = () => {

    const { id } = useParams();
    //console.log(id);

    const [formData, setFormData] = useState({
        title: '',
        question: '',
        hint: '',
        option1: '',
        option2: '',
        option3: '',
        option4: '',
        image: '',
        radioOption: ''
    });

    useEffect(() => {
        if (id) {
            getSpecificQuestion({ _id: id })
                .then((res) => {
                    //console.log(res);
                    setFormData(res[0]);
                })
        }
    }, [])

    const previewSelectedData = () => {
        const select_image = document.getElementById('select_image');

        const file = select_image.files[0];

        if (file) {

            const preview_image = document.getElementById('preview_image');

            const fileReader = new FileReader();
            fileReader.readAsDataURL(file);

            fileReader.addEventListener('load', (e) => {
                preview_image.src = e.target.result;
                setFormData({ ...formData, ['image']: e.target.result })
            })
        }
    }

    const onChange = (e) => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSelectAns = (v) => {
        setFormData({ ...formData, ['radioOption']: v });
    }

    const onSubmit = (e) => {
        e.preventDefault();
        if(!id) addQuestion(formData);
        if(id) updateQuestion(formData);
    }

    return (
        <>
            <QuizNavBar />
            <Container className="container">
                <Form onSubmit={onSubmit}>
                    <Row>
                        {/* ---------- select image file ---------- */}

                        <Col lg={4} md={4} sm={4}>
                            <Row>
                                <h3>Related Image</h3>
                                <input id="select_image" type='file' accept="image/*" onChange={previewSelectedData} />
                            </Row>

                            {/* ---------- preview Image ---------- */}

                            <Row className="mt-5">
                                <h3>Preview</h3>

                                <Figure >
                                    <Figure.Image
                                        id='preview_image'
                                        src={formData.image}
                                        width='100%'
                                        alt="select image"
                                    />
                                    <Figure.Caption className="d-flex justify-content-center" style={{ color: 'green' }}>
                                        Selected image
                                    </Figure.Caption>
                                </Figure>
                            </Row>
                        </Col>


                        {/* ---------- Question Template ---------- */}
                        <Col md={{ offset: 1 }} lg={{ offset: 1 }}>

                            <h2 className="d-flex justify-content-sm-center">Question Template</h2>
                            <Row>
                                <Form.Label>Title</Form.Label>
                                <Form.Control
                                    type="text"
                                    name='title'
                                    onChange={onChange}
                                    value={formData.title}
                                    placeholder="Title" />

                            </Row>
                            <br />
                            <Row>
                                <Form.Label>Question ?</Form.Label>
                                <Form.Control
                                    as="textarea"
                                    type="text"
                                    name='question'
                                    onChange={onChange}
                                    value={formData.question}
                                    placeholder="Question ?" />

                            </Row>
                            <br />
                            <Row>
                                <Form.Label>Hint</Form.Label>
                                <Form.Control
                                    type="text"
                                    name='hint'
                                    onChange={onChange}
                                    value={formData.hint}
                                    placeholder="Hint" />
                            </Row>
                            <br />
                            {/* ---------- Options ---------- */}
                            <Row>
                                <Col>
                                    <InputGroup className="mb-3">
                                        <InputGroup.Text>
                                            <Form.Check
                                                name='radioOption'
                                                type={'radio'}
                                                onChange={() => onSelectAns('option1')}
                                            />
                                        </InputGroup.Text>
                                        <FormControl
                                            placeholder="Option 1"
                                            name='option1'
                                            value={formData.option1}
                                            onChange={onChange}
                                        />
                                    </InputGroup>

                                    <InputGroup className="mb-3">
                                        <InputGroup.Text>
                                            <Form.Check
                                                name='radioOption'
                                                type={'radio'}
                                                onChange={() => onSelectAns('option2')}
                                            />
                                        </InputGroup.Text>
                                        <FormControl
                                            placeholder="Option 2"
                                            name='option2'
                                            value={formData.option2}
                                            onChange={onChange}
                                        />
                                    </InputGroup>
                                </Col>

                                <Col>
                                    <InputGroup className="mb-3">
                                        <InputGroup.Text>
                                            <Form.Check
                                                name='radioOption'
                                                type={'radio'}
                                                onChange={() => onSelectAns("option3")}
                                            />
                                        </InputGroup.Text>

                                        <FormControl
                                            placeholder="Option 3"
                                            name='option3'
                                            value={formData.option3}
                                            onChange={onChange}
                                        />
                                    </InputGroup>

                                    <InputGroup className="mb-3">
                                        <InputGroup.Text>
                                            <Form.Check
                                                name='radioOption'
                                                type={'radio'}
                                                onChange={() => onSelectAns("option4")}
                                            />
                                        </InputGroup.Text>
                                        <FormControl
                                            placeholder="Option 4"
                                            name='option4'
                                            value={formData.option4}
                                            onChange={onChange}
                                        />
                                    </InputGroup>
                                </Col>
                                {/* ---------- Options ends here ---------- */}
                            </Row>
                            <Row className="mt-4">
                                <Col md={{ span: 4, offset: 8 }} lg={{ span: 4, offset: 8 }}>
                                    <FormControl as={Button} type='submit' variant='success'>Save</FormControl>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                </Form>
            </Container>
        </>
    )
}

export default AddQForm;