import React from "react";
import { Container, Row, Col, Button } from "react-bootstrap";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { deleteQuestion } from "../../Actions/Quiz/QuizAction";
import PopModal from "../../Components/Modal/PopModal";
import QuizNavBar from "../../Components/NavBar/QuizNavBar";
import { DELETE } from "../../Utils/Constants";

const Admin = () => {

    const questions = useSelector((state) => state.qStore.questions);
    const navigate = useNavigate();

    const handleEdit = (id) => {
        navigate(`edit/${id}`);
    }

    return (
        <>
            <QuizNavBar />
            <div className="container"></div>
            <Container style={{marginTop:'2%'}} className='container'>

                {
                    questions.map((v, i) => {
                        return (
                            <div key={i} style={{ marginBottom:'1%' }}>
                                <Row>
                                    <Col md={2} ><img alt={'img'} src={v.image} style={imgStyle} /></Col>
                                    <Col>
                                        <h3>{v.question}</h3>
                                        <h6>Hint :&nbsp;{v.hint}</h6>
                                    </Col>

                                    <Col>
                                        <Row>
                                            <Col>
                                            <Button variant="primary" onClick={()=>handleEdit(v._id)} className="me-1 p-1">
                                                Edit
                                            </Button>
                                            <PopModal
                                                mode={DELETE}
                                                ctlBtnVariant={'danger'}
                                                modalBtnText={'Delete'}
                                                modelTitle={'Delete'}
                                                ctlBtnText={'Delete'}
                                                message={'permanently delete question ?'}
                                                execFunc={()=>deleteQuestion({_id:v._id})}
                                            />
                                            </Col>
                                        </Row>
                                    </Col>
                                </Row >
                            </div>
                        )
                    })
                }
            </Container>
        </>
    )
}


export default Admin;


const imgStyle = {
    width: '100%',
    height: '100%',
};